<template>
  <div class="root">
    <div class="stage">
        <div
        @mousedown="onMouseDown($event)"
        @mousemove="onMouseMove($event)"
        @mouseup="onMouseUp($event)"
        style="position: absolute; top: 0; right: 0; bottom: 0; right: 0; width: 100%; height: 100%;">
        <svg :style="{
          'position': 'absolute',
          'top': '0',
          'right': '0',
          'bottom': '0',
          'right': '0',
          'width': '100%',
          'height': '100%',
          'cursor': 'none',
        }">
          <!-- [記憶フェーズ] -->
          <template v-if="phase === 'MEMORYING'">
            <!-- 単位円 --><circle v-if="unitCircle" :cx="465" :cy="330" r="220" fill="transparent" stroke="#cccccc" stroke-width="0.5" />
            <!-- ヒント点群 --><Shapes :shapes="cp.projectedHintShapes" colorCSS="black" />
            <!-- 答え点群 --><Shapes :shapes="cp.projectedCorrectShapes" colorCSS="black" />
          </template>
          <!-- [変換フェーズ] -->
          <template v-if="phase === 'TRANSFORMING'">
            <!-- 単位円 --><circle v-if="unitCircle" :cx="465 + 110*cp.tweeningTransform.translate.x" :cy="330 - 110*cp.tweeningTransform.translate.y" r="220" fill="transparent" stroke="#cccccc" stroke-width="0.5" />
            <!-- ヒント点群 --><Shapes :shapes="cp.projectedHintShapes" colorCSS="black" />
            <!-- 答え点群 --><Shapes :shapes="cp.projectedCorrectShapes" :colorCSS="correctShapesColorCSS" />
          </template>
          <!-- [解答フェーズ] -->
          <template v-if="phase === 'ANSWERING'">
            <!-- 単位円 --><circle v-if="unitCircle" :cx="465 + 110*cp.transform.translate.x" :cy="330 - 110*cp.tweeningTransform.translate.y" r="220" fill="transparent" stroke="#cccccc" stroke-width="0.5" />
            <!-- ヒント点群 --><Shapes :shapes="cp.projectedHintShapes" colorCSS="black" />
            <!-- カーソル --><circle :cx="aaaaa" :cy="bbbbb" r="3" fill="blue" />
            <!-- ユーザの解答 -->
            <template v-for="stroke in cp.userAnswer.strokes">
              <template v-for="(point, index) in stroke.points">
                <!-- 点 -->
                <circle
                  :key="index"
                  :cx="scaleAndTranslate(930, 660, 110, point.x, 0).x"
                  :cy="scaleAndTranslate(930, 660, 110, 0, point.y).y"
                  r="1.5"
                  fill="blue" />
              </template>
            </template>
          </template>
          <!-- [答え合わせフェーズ] -->
          <template v-if="phase === 'MARKING'">
            <!-- 許容誤差範囲 --><AcceptableArea :shapes="cp.projectedCorrectShapes" colorCSS="rgb(240, 240, 240)" :acceptableErrorLength="0.1 " />
            <!-- 単位円 --><circle v-if="unitCircle" :cx="465 + 110*cp.tweeningTransform.translate.x" :cy="330 - 110*cp.tweeningTransform.translate.y" r="220" fill="transparent" stroke="#cccccc" stroke-width="0.5" />
            <!-- ヒント点群 --><Shapes :shapes="cp.projectedHintShapes" colorCSS="black" />
            <!-- 答え点群 --><Shapes :shapes="cp.projectedCorrectShapes" colorCSS="black" :acceptableErrorLength="0.2" />
            <!-- ユーザの解答 -->
            <template v-for="stroke in cp.userAnswer.strokes">
              <template v-for="(point, index) in stroke.points">
                <!-- 点 -->
                <circle
                  :key="index"
                  :cx="scaleAndTranslate(930, 660, 110, point.x, 0).x"
                  :cy="scaleAndTranslate(930, 660, 110, 0, point.y).y"
                  r="1.5"
                  fill="blue" />
              </template>
            </template>
          </template>
        </svg>
      </div>
    </div>
    <div class="debug-panel">
      <input type="button" value="Go to MEMORYING" @click="phase = 'MEMORYING'" /><br>
      <input type="button" value="Toggle unit circle" @click="unitCircle = !unitCircle" /><br>
      <input type="button" value="Reset session" @click="resetSession()"/>
    </div>
    <div v-show="phase === 'MEMORYING'" class="memoried-button" @click="nextPhase()">おぼえた</div>
    <div v-show="phase === 'ANSWERING'" class="memoried-button" @click="nextPhase()">答える</div>
    <div v-show="phase === 'MARKING'" class="memoried-button" @click="nextPhase()">次の問題へ</div>
    <div class="phase-indicator">{{phase}}</div>
  </div>
</template>

<script>
import _ from 'lodash';
import keycode from 'keycode';
import Color from 'color-js';
import { SVGPathData, encodeSVGPath } from 'svg-pathdata';
import { vec2 } from 'gl-matrix';
import TWEEN from '@tweenjs/tween.js';
import { Howl, Howler } from 'howler';

import Shapes from './Shapes';
import AcceptableArea from './AcceptableArea';

const transformVector = (x, y, transform) => {
  return vec2.add(
    vec2.create(), // Output receiver
    vec2.rotate( // Target vector
      vec2.create(), // Output receiver
      vec2.fromValues(x, y), // Target vector
      vec2.fromValues(0, 0), // Origin of rotation
      transform.rotate.amount, // Amount of rotation [rad]
    ),
    vec2.fromValues(transform.translate.x, transform.translate.y), // Amount of addition
  );
};

const transformShapes = (shapes, transform) => {
  return shapes.map((shape) => {
    switch (shape.type) {
      case 'point': {
        const transformedVector = transformVector(shape.x, shape.y, transform);

        return {
          type: 'point',
          x: transformedVector[0],
          y: transformedVector[1],
        };
      }
      case 'line': {
        const transformedStartVector = transformVector(shape.points.start.x, shape.points.start.y, transform);
        const transformedEndVector = transformVector(shape.points.end.x, shape.points.end.y, transform);

        return {
          type: 'line',
          points: {
            start: {
              x: transformedStartVector[0],
              y: transformedStartVector[1],
            },
            end: {
              x: transformedEndVector[0],
              y: transformedEndVector[1],
            },
          },
        };
      }
      case 'polyline': {
        return {
          type: 'polyline',
          points: shape.points.map((point) => {
            const transformedVector = transformVector(point[0], point[1], transform);

            return transformedVector;
          }),
        };
      }
      case 'path': {
        return {
          type: 'path',
          svgCommands: shape.svgCommands.map((command) => {
            switch (command.type) {
              case SVGPathData.MOVE_TO: {
                if (!command.relative) {
                  const transformedVector = transformVector(command.x, command.y, transform);
                  return { type: SVGPathData.MOVE_TO, x: transformedVector[0], y: transformedVector[1] };
                } else {
                  throw new Error('Relative coordinates are not supported.');
                }
              }
              case SVGPathData.LINE_TO: {
                if (!command.relative) {
                  const transformedVector = transformVector(command.x, command.y, transform);
                  return { type: SVGPathData.LINE_TO, x: transformedVector[0], y: transformedVector[1] };
                } else {
                  throw new Error('Relative coordinates are not supported.');
                }
              }
              case SVGPathData.CURVE_TO: {
                if (!command.relative) {
                  const transformedVectorX1Y1 = transformVector(command.x1, command.y1, transform);
                  const transformedVectorX2Y2 = transformVector(command.x2, command.y2, transform);
                  const transformedVector = transformVector(command.x, command.y, transform);
                  return {
                    type: SVGPathData.CURVE_TO,
                    x1: transformedVectorX1Y1[0],
                    y1: transformedVectorX1Y1[1],
                    x2: transformedVectorX2Y2[0],
                    y2: transformedVectorX2Y2[1],
                    x: transformedVector[0],
                    y: transformedVector[1],
                  };
                } else {
                  throw new Error('Relative coordinates are not supported.');
                }
              }
              case SVGPathData.SMOOTH_CURVE_TO: {
                if (!command.relative) {
                  const transformedVectorX2Y2 = transformVector(command.x2, command.y2, transform);
                  const transformedVector = transformVector(command.x, command.y, transform);
                  return {
                    type: SVGPathData.SMOOTH_CURVE_TO,
                    x2: transformedVectorX2Y2[0],
                    y2: transformedVectorX2Y2[1],
                    x: transformedVector[0],
                    y: transformedVector[1],
                  };
                } else {
                  throw new Error('Relative coordinates are not supported.');
                }
              }
              default: {
                console.warn(`Unknown command type: ${command.type}`);
                return command;
              }
            }
          }),
        };
      }
      default: {
        throw new Error('Unknown shape type');
      }
    }
  });
};

const tween = ({ from, to, duration, easing, onUpdate, onComplete }) => {
  let frameHandler;

  // Handles updating the tween on each frame.
  const animate = (currentTime) => {
    TWEEN.update(currentTime);
    frameHandler = requestAnimationFrame(animate);
  };

  new TWEEN.Tween(from)
  .to(to, duration)
  .easing(easing)
  // Be careful to not to do too much here! It will slow down the app.
  .onUpdate(onUpdate)
  .onComplete(() => {
    onComplete();

    // Make sure to clean up after ourselves.
    cancelAnimationFrame(frameHandler);
  })
  // This actually starts the tween.
  .start();

  frameHandler = requestAnimationFrame(animate);
};

const scaleAndTranslate = (width, height, unit, x, y) => {
  return {
    x: (width / 2) + (unit * x),
    y: (height / 2) - (unit * y),
  };
};

const scaleAndTranslateInverse = (width, height, unit, x, y) => {
  return {
    x: (x - (width / 2)) / unit,
    y: -(y - (height / 2)) / unit,
  };
};

// Setup the new Howl.
const penDownSound = new Howl({
  src: ['static/pop.wav'],
});
const penMoveSound = new Howl({
  src: ['static/pop.wav'],
});

export default {
  data() {
    return {
      phase: 'MEMORYING',
      unitCircle: true,
      aaaaa: 50,
      bbbbb: 50,
      session: { // セッション
        problems: [
          { // 問題
            hintShapes: [ // 論理座標（変換前）
            ],
            correctShapes: [ // 論理座標（変換前）
            ],
            tweeningTransform: {
            },
            tweeningHintShapes: [ // tween 中の値
            ],
            tweeningCorrectShapes: [ // tween 中の値
            ],
            transform: {
              // 平行移動量
              translate: {
                x: 0,
                y: 0,
              },
              // 回転量 [rad]
              rotate: {
                amount: 0,
              },
            },
            projectedHintShapes: [ // 論理座標（投影変換後）
            ],
            projectedCorrectShapes: [ // 論理座標（投影変換後）
            ],
            correctShapesColor: null,
            userAnswer: { // ユーザの解答
              strokes: [],
              canceledStrokes: [],
            },
            others: {
              // 初期位置
              // 長さ1あたりのピクセル数
              // 点の表示ピクセル数
            },
          },
        ],
        currentProblemIndex: 0,
      },
      isMouseDown: false,
    };
  },
  computed: {
    cp() { // = current problem
      return this.session.problems[this.session.currentProblemIndex];
    },
    correctShapesColorCSS() {
      return new Color({
        red: this.cp.correctShapesColor.red,
        green: this.cp.correctShapesColor.green,
        blue: this.cp.correctShapesColor.blue,
        alpha: this.cp.correctShapesColor.alpha,
      }).toCSS();
    },
  },
  created() {
    this.resetSession();
  },
  mounted() {
    window.addEventListener('keydown', (e) => {
      if (e.keyCode === keycode('space')) {
        this.nextPhase();
        e.preventDefault(); // Prevent scroll
      }
    });
    window.addEventListener('keydown', (e) => {
      if (e.ctrlKey && e.keyCode === keycode('z')) {
        if (!e.shiftKey) {
          this.undo();
        } else {
          this.redo();
        }
        e.preventDefault();
      }
    });
  },
  watch: {
    phase(newVal, oldVal) {
      console.log(`${newVal} ${oldVal} `);

      // 特定の状態に遷移したときに必ず行う処理
      switch (newVal) {
        case 'MEMORYING': {
          this.resetSession();
          break;
        }
        case 'TRANSFORMING': {
          // トゥイーン中のパラメータを格納しておくオブジェクト
          this.cp.tweeningTransform = {
            translate: {
              x: 0,
              y: 0,
            },
            rotate: {
              amount: 0,
            },
          };
          // 平行移動トゥイーンを実行
          tween({
            from: this.cp.tweeningTransform.translate,
            to: this.cp.transform.translate,
            duration: 500,
            easing: TWEEN.Easing.Quadratic.InOut,
            onUpdate: () => {
              this.updateShapes(this.cp);
            },
            onComplete: () => {
              this.cp.userAnswer.strokes.length = 0;
              this.cp.userAnswer.canceledStrokes.length = 0;
              this.phase = 'ANSWERING';
            },
          });
          // 回転トゥイーンを実行
          tween({
            from: this.cp.tweeningTransform.rotate,
            to: this.cp.transform.rotate,
            duration: 500,
            easing: TWEEN.Easing.Quadratic.InOut,
            onUpdate: () => {
              this.updateShapes(this.cp);
              this.$forceUpdate();
            },
            onComplete: () => {
            },
          });
          // 変色トゥイーンを実行
          tween({
            from: this.cp.correctShapesColor,
            to: _.merge(_.clone(this.cp.correctShapesColor), { alpha: 0 }),
            duration: 500,
            easing: TWEEN.Easing.Quadratic.Out,
            onUpdate: () => {
            },
            onComplete: () => {
            },
          });
          break;
        }
        case 'MARKING': {
          _.merge(this.cp.correctShapesColor, { alpha: 1 });
          break;
        }
        default: {
          break;
        }
      }
      // 特定の状態から特定の状態に遷移したときに行う処理
      switch (oldVal) {
        case 'MEMORYING': {
          switch (newVal) {
            case 'TRANSFORMING': {
              break;
            }
            default: {
              break;
            }
          }
          break;
        }
        default: {
          break;
        }
      }
    },
  },
  methods: {
    scaleAndTranslate,
    resetSession() {
      // ヒント点群と答え点群を削除
      this.cp.hintShapes.length = 0;
      this.cp.correctShapes.length = 0;

      // ヒント点群に折れ線を追加
      // {
        // const direction = 2.0 * Math.PI * Math.random(); // 360° からランダムに方向を選択
        // const firstPoint = vec2.rotate(
        //   vec2.create(), // Output receiver
        //   vec2.fromValues(1, 0), // Target vector
        //   vec2.fromValues(0, 0), // Origin of rotation
        //   direction, // Amount of rotation
        // );
        // const secondPoint = vec2.rotate(
        //   vec2.create(), // Output receiver
        //   vec2.fromValues(1, 0), // Target vector
        //   vec2.fromValues(0, 0), // Origin of rotation
        //   direction + Math.PI, // Amount of rotation
        // );
        // const thirdPoint = vec2.rotate(
        //   vec2.create(), // Output receiver
        //   vec2.fromValues(2.0 * Math.random(), 0), // Target vector
        //   vec2.fromValues(0, 0), // Origin of rotation
        //   2.0 * Math.PI * Math.random(), // Amount of rotation
        // );

        // this.cp.hintShapes.push({
        //   type: 'polyline',
        //   points: [firstPoint, thirdPoint, secondPoint],
        // });
      // }

      // 答え点群に曲線を追加（＋曲線の2端点をヒント点群として追加）
      {
        const direction = 2.0 * Math.PI * Math.random(); // 360° からランダムに方向を選択
        const firstPoint = vec2.rotate(
          vec2.create(), // Output receiver
          vec2.fromValues(1, 0), // Target vector
          vec2.fromValues(0, 0), // Origin of rotation
          direction, // Amount of rotation
        );
        const secondPoint = vec2.rotate(
          vec2.create(), // Output receiver
          vec2.fromValues(1, 0), // Target vector
          vec2.fromValues(0, 0), // Origin of rotation
          direction + Math.PI, // Amount of rotation
        );

        const controlPoint1 = vec2.rotate(
          vec2.create(), // Output receiver
          vec2.fromValues(2 * Math.random(), 0), // Target vector
          vec2.fromValues(0, 0), // Origin of rotation
          2.0 * Math.PI * Math.random(), // Amount of rotation
        );
        const controlPoint2 = vec2.rotate(
          vec2.create(), // Output receiver
          vec2.fromValues(2 * Math.random(), 0), // Target vector
          vec2.fromValues(0, 0), // Origin of rotation
          2.0 * Math.PI * Math.random(), // Amount of rotation
        );

        const svgCommands = [
          {
            type: SVGPathData.MOVE_TO,
            relative: false,
            x: firstPoint[0],
            y: firstPoint[1],
          },
          {
            type: SVGPathData.CURVE_TO,
            relative: false,
            x1: controlPoint1[0],
            y1: controlPoint1[1],
            x2: controlPoint2[0],
            y2: controlPoint2[1],
            x: secondPoint[0],
            y: secondPoint[1],
          },
          // { type: SVGPathData.CLOSE_PATH },
        ];

        this.cp.hintShapes.push({
          type: 'point',
          x: firstPoint[0],
          y: firstPoint[1],
        });
        this.cp.hintShapes.push({
          type: 'point',
          x: secondPoint[0],
          y: secondPoint[1],
        });
        this.cp.correctShapes.push({
          type: 'path',
          svgCommands: svgCommands,
        });
      }

      // 変形内容を設定
      this.cp.transform.translate.x = 0;
      this.cp.transform.translate.y = 0;
      this.cp.transform.rotate.amount = 0;
      this.cp.transform.translate.x = (((930 / 2) / 110) - 2) * ((Math.random() * 2) - 1);
      this.cp.transform.translate.y = (((660 / 2) / 110) - 2) * ((Math.random() * 2) - 1);
      // this.cp.transform.rotate.amount = Math.PI * ((2 * Math.random()) - 1); // [-π, π)

      // 変形中のヒント点群と答え点群を初期化
      this.cp.tweeningHintShapes = _.cloneDeep(this.cp.hintShapes);
      this.cp.tweeningCorrectShapes = _.cloneDeep(this.cp.correctShapes);

      // 投影変換実行後のヒント点群と答え点群を初期化
      this.cp.projectedHintShapes = _.cloneDeep(this.cp.hintShapes);
      this.cp.projectedCorrectShapes = _.cloneDeep(this.cp.correctShapes);

      // 答え点群の色を設定
      this.cp.correctShapesColor = { red: 0, green: 0, blue: 0, alpha: 1 };
    },
    updateShapes(problem) {
      // ヒント点群・答え点群に変形を実行
      {
        _.merge( // deep assign
          problem.tweeningHintShapes,
          transformShapes(
            problem.hintShapes,
            problem.tweeningTransform,
          ),
        );
        _.merge( // deep assign
          problem.tweeningCorrectShapes,
          transformShapes(
            problem.correctShapes,
            problem.tweeningTransform,
          ),
        );
      }

      // ヒント点群・答え点群に投影変換を実行
      {
        _.merge( // deep assign
          problem.projectedHintShapes,
          problem.tweeningHintShapes,
          // projectShapes(problem.tweeningHintShapes),
        );
        _.merge( // deep assign
          problem.projectedCorrectShapes,
          problem.tweeningCorrectShapes,
          // projectShapes(problem.tweeningCorrectShapes),
        );
      }
    },
    nextPhase() {
      switch (this.phase) {
        case 'MEMORYING': {
          this.phase = 'TRANSFORMING';
          break;
        }
        case 'ANSWERING': {
          this.phase = 'MARKING';
          break;
        }
        case 'MARKING': {
          this.phase = 'MEMORYING';
          break;
        }
        default: {
          break;
        }
      }
    },
    undo() {
      // console.log('undo');
      if (!this.isMouseDown) { // 新しい線を描いている途中はアンドゥを受け付けない
        if (this.cp.userAnswer.strokes.length > 0) {
          // strokes の末尾の要素を canceledStrokes の先頭に移動
          this.cp.userAnswer.canceledStrokes.unshift(
            this.cp.userAnswer.strokes.pop(),
          );
        }
      }
    },
    redo() {
      // console.log('redo');
      if (!this.isMouseDown) { // 新しい線を描いている途中はリドゥを受け付けない
        if (this.cp.userAnswer.canceledStrokes.length > 0) {
          // canceledStrokes の先頭の要素を strokes の末尾に移動
          this.cp.userAnswer.strokes.push(
            this.cp.userAnswer.canceledStrokes.shift(),
          );
        }
      }
    },
    onMouseDown($event) {
      this.isMouseDown = true;
      switch (this.phase) {
        case 'ANSWERING': {
          const newStroke = { points: [] };
          newStroke.points.push(scaleAndTranslateInverse(930, 660, 110, $event.offsetX, $event.offsetY));
          penDownSound.play();
          this.cp.userAnswer.strokes.push(newStroke);
          this.cp.userAnswer.canceledStrokes.length = 0; // 新しい線を描き始めたらリドゥ用情報を破壊
          break;
        }
        default: {
          break;
        }
      }
    },
    onMouseMove($event) {
      this.aaaaa = $event.offsetX;
      this.bbbbb = $event.offsetY;
      switch (this.phase) {
        case 'ANSWERING': {
          if (this.isMouseDown) {
            const currentStroke = this.cp.userAnswer.strokes[this.cp.userAnswer.strokes.length - 1];
            currentStroke.points.push(scaleAndTranslateInverse(930, 660, 110, $event.offsetX, $event.offsetY));

            // 入力音を再生
            // console.log(`Playing: ${penMoveSound.playing()}, Pos: ${penMoveSound.seek()}`);
            if (penMoveSound.playing()) {
              const currentPosInSec = penMoveSound.seek();
              if (currentPosInSec === 0 // 再生が終わっている場合
                || currentPosInSec >= 0.04) { // 一定時間が経過している場合
                // console.log(`seek`);
                penMoveSound.seek(0);
              }
            } else {
              // console.log(`play`);
              penMoveSound.play();
            }
          }
          break;
        }
        default: {
          break;
        }
      }
    },
    onMouseUp($event) {
      this.isMouseDown = false;
      switch (this.phase) {
        case 'ANSWERING': {
          break;
        }
        default: {
          break;
        }
      }
    },
  },
  components: {
    Shapes,
    AcceptableArea,
  },
};
</script>

<style scoped>
.root {
  width: 100%;
  height: 100%;
  background-color: #ccc;
  font-family: 'メイリオ', sans-serif;
  user-select: none;
}

.stage {
  position: absolute;
  top: 30px;
  left: 30px;
  bottom: 30px;
  width: 930px;
  background-color: white;
}

.debug-panel {
  position: absolute;
  top: 32px;
  left: calc(1000px - 8px);

  box-sizing: border-box;
  width: calc(240px + 16px);
  height: 500px;
  padding: 16px;
  background-color: #eee;
  color: white;
}

.memoried-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 600px;
  left: 1000px;
  width: 240px;
  height: 80px;
  background-color: #333;
  color: white;
  font-size: 32px;
  cursor: pointer;
}

