import Vue from 'vue';
import Scene from '@/components/MemoryPointsTrainingScene';

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: `
    <div style="
      border: 32px solid transparent;
      width: 1280px;
      margin: 32px auto 0 auto;
    ">
      <div style="
        width: 1280px;
        height: 720px;
        display: flex;
        margin: 0 auto;
        justify-content: center;
        align-items: center;
        position: relative;
        ">
        <Scene />
      </div>
    </div>
  `,
  components: { Scene },
});



