<template>
  <g>
    <template v-for="(shape, index) in shapes">
      <!-- 点 -->
      <template v-if="shape.type === 'point'">
        <circle :key="index" :cx="(930/2) + 110*shape.x" :cy="(660/2) - 110*shape.y" :r="acceptableErrorLength * 110" :style="{ 'fill': colorCSS }" />
      </template>
      <!-- 線分 -->
      <template v-if="shape.type === 'line'">
        <g :key="index" :style="{ 'stroke': colorCSS }">
          <line
            :x1="(930/2) + 110*shape.points.start.x"
            :y1="(660/2) - 110*shape.points.start.y"
            :x2="(930/2) + 110*shape.points.end.x"
            :y2="(660/2) - 110*shape.points.end.y"
            :stroke-width="acceptableErrorLength * 110 * 2" />
        </g>
      </template>
      <!-- 折れ線 -->
      <template v-if="shape.type === 'polyline'">
        <polyline
          :key="index"
          fill="none"
          :points="convertToSvgPoints(930, 660, 110, shape.points)"
          :style="{ 'stroke': colorCSS }"
          :stroke-width="acceptableErrorLength * 2 * 110" />
      </template>
      <!-- パス -->
      <template v-if="shape.type === 'path'">
        <path
          :key="index"
          fill="none"
          :d="convertToSvgPath(930, 660, 110, shape.svgCommands)"
          :style="{ 'stroke': colorCSS }"
          :stroke-width="acceptableErrorLength * 2 * 110"
          stroke-linecap="round" />
      </template>
    </template>
  </g>
</template>

<script>
import { SVGPathData, encodeSVGPath } from 'svg-pathdata';

const scaleAndTranslate = (width, height, unit, x, y) => {
  return {
    x: (width / 2) + (unit * x),
    y: (height / 2) - (unit * y),
  };
};

export default {
  props: [
    'shapes',
    'colorCSS',
    'acceptableErrorLength',
  ],
  methods: {
    convertToSvgPoints(width, height, unit, points) {
      return points.map((v) => {
        const u = scaleAndTranslate(width, height, unit, v[0], v[1]);
        return `${u.x},${u.y}`;
      }).join(' ');
    },
    convertToSvgPath(width, height, unit, svgCommands) {
      const convertedSvgCommands = svgCommands.map((command) => {
        switch (command.type) {
          case SVGPathData.MOVE_TO: {
            if (!command.relative) {
              const u = scaleAndTranslate(width, height, unit, command.x, command.y);
              return { type: SVGPathData.MOVE_TO, x: u.x, y: u.y };
            } else {
              throw new Error('Relative coordinates are not supported.');
            }
          }
          case SVGPathData.LINE_TO: {
            if (!command.relative) {
              const u = scaleAndTranslate(width, height, unit, command.x, command.y);
              return { type: SVGPathData.LINE_TO, x: u.x, y: u.y };
            } else {
              throw new Error('Relative coordinates are not supported.');
            }
          }
          case SVGPathData.SMOOTH_CURVE_TO: {
            if (!command.relative) {
              const uX2Y2 = scaleAndTranslate(width, height, unit, command.x2, command.y2);
              const u = scaleAndTranslate(width, height, unit, command.x, command.y);
              return {
                type: SVGPathData.SMOOTH_CURVE_TO,
                x2: uX2Y2.x,
                y2: uX2Y2.y,
                x: u.x,
                y: u.y,
              };
            } else {
              console.warn(`Unknown command type: ${command.type}`);
              throw new Error('Relative coordinates are not supported.');
            }
          }
          case SVGPathData.CURVE_TO: {
            if (!command.relative) {
              const uX1Y1 = scaleAndTranslate(width, height, unit, command.x1, command.y1);
              const uX2Y2 = scaleAndTranslate(width, height, unit, command.x2, command.y2);
              const u = scaleAndTranslate(width, height, unit, command.x, command.y);
              return {
                type: SVGPathData.CURVE_TO,
                x1: uX1Y1.x,
                y1: uX1Y1.y,
                x2: uX2Y2.x,
                y2: uX2Y2.y,
                x: u.x,
                y: u.y,
              };
            } else {
              console.warn(`Unknown command type: ${command.type}`);
              throw new Error('Relative coordinates are not supported.');
            }
          }
          default: {
            return command;
          }
        }
      });
      return encodeSVGPath(convertedSvgCommands);
    },
  },
};
</script>

<style>

</style>



